<template>
  <view>
    <view class="container diy_view">
      <view>
        <view>
          <view class="">
            <uni-forms :modelValue="form"  v-if="is_view()">

              <uni-forms-item v-if="$check_field('get','food_name') || $check_field('add','food_name') || $check_field('set','food_name')" label="美食名称" name="food_name">
                                <uni-easyinput type="text" v-model="form['food_name']" v-if="user_group === '管理员' || (form['food_information_id'] && $check_field('set','food_name')) || (!form['food_information_id'] && $check_field('add','food_name'))" :disabled="disabledObj['food_name_isDisabled']" />
                <!-- 仅查看 -->
                <text v-else-if="$check_field('get','food_name')">
                  {{ form['food_name'] }}
                </text>
                            </uni-forms-item>
              <uni-forms-item v-if="$check_field('get','cover') || $check_field('add','cover') || $check_field('set','cover')" label="封面" name="cover">
                        <!-- 修改权限 -->
                <view class="diy_field diy_img" v-if="form['cover'] && $check_field('set','cover')">
                  <image v-if="disabledObj['cover_isDisabled']" :src="$fullUrl(form['cover'])" />
                  <image v-if="!disabledObj['cover_isDisabled']" :src="$fullUrl(form['cover'])" @click="change_img('cover')" />
                </view>
                <!-- 添加权限 -->
                <view class="diy_field diy_img" v-else-if="!form['cover'] && $check_field('add','cover')">
                  <view v-if="disabledObj['cover_isDisabled']" class="btn_add_img">
                    <text>+</text>
                  </view>
                  <view v-if="!disabledObj['cover_isDisabled']" class="btn_add_img" @click="change_img('cover')">
                    <text>+</text>
                  </view>
                </view>
                <!-- 查询权限 -->
                <view class="diy_field diy_img" v-else-if="$check_field('get','cover')">
                  <image :src="$fullUrl(form['cover'])" />
                </view>
                    </uni-forms-item>
              <uni-forms-item v-if="$check_field('get','food_unit_price') || $check_field('add','food_unit_price') || $check_field('set','food_unit_price')" label="美食单价" name="food_unit_price">
                                <uni-easyinput type="text" v-model="form['food_unit_price']" v-if="user_group === '管理员' || (form['food_information_id'] && $check_field('set','food_unit_price')) || (!form['food_information_id'] && $check_field('add','food_unit_price'))" :disabled="disabledObj['food_unit_price_isDisabled']" />
                <!-- 仅查看 -->
                <text v-else-if="$check_field('get','food_unit_price')">
                  {{ form['food_unit_price'] }}
                </text>
                            </uni-forms-item>
              <uni-forms-item v-if="$check_field('get','flavor') || $check_field('add','flavor') || $check_field('set','flavor')" label="口味" name="flavor">
                                <uni-easyinput type="text" v-model="form['flavor']" v-if="user_group === '管理员' || (form['food_information_id'] && $check_field('set','flavor')) || (!form['food_information_id'] && $check_field('add','flavor'))" :disabled="disabledObj['flavor_isDisabled']" />
                <!-- 仅查看 -->
                <text v-else-if="$check_field('get','flavor')">
                  {{ form['flavor'] }}
                </text>
                            </uni-forms-item>
              <uni-forms-item v-if="$check_field('get','food_materials') || $check_field('add','food_materials') || $check_field('set','food_materials')" label="美食材料" name="food_materials">
                                <uni-easyinput type="text" v-model="form['food_materials']" v-if="user_group === '管理员' || (form['food_information_id'] && $check_field('set','food_materials')) || (!form['food_information_id'] && $check_field('add','food_materials'))" :disabled="disabledObj['food_materials_isDisabled']" />
                <!-- 仅查看 -->
                <text v-else-if="$check_field('get','food_materials')">
                  {{ form['food_materials'] }}
                </text>
                            </uni-forms-item>
              <uni-forms-item v-if="$check_field('get','details') || $check_field('add','details') || $check_field('set','details')" label="详情介绍" name="details">
                      </uni-forms-item>


            </uni-forms>
            <view class="form_button">
              <button size="mini" type="primary" @click="submit()" class="primary_btn">提交</button>
              <button size="mini" @click="cancel()">取消</button>
            </view>
          </view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import mixin from "@/libs/mixins/page.js";

export default {
  mixins: [mixin],
  data() {
    return {
      field: "food_information_id",
      url_add: "~/api/food_information/add?",
      url_set: "~/api/food_information/set?",
      url_get_obj: "~/api/food_information/get_obj?",
      url_upload: "~/api/food_information/upload?",

      query: {
        "food_information_id": 0,
      },

      form: {
            "food_name":  '', // 美食名称
                    "cover":  '', // 封面
                    "food_unit_price":  0 , // 美食单价
                    "flavor":  '', // 口味
                    "food_materials":  '', // 美食材料
                    "details":  '', // 详情介绍
                                "food_information_id": 0, // ID
                
              },
          disabledObj:{
                        "food_name_isDisabled": false,
                                "cover_isDisabled": false,
                                                    "flavor_isDisabled": false,
                                "food_materials_isDisabled": false,
                                "details_isDisabled": false,
                                  },
                                                                                                                                              }
  },
  methods: {
    changeLog(v,value){
      this.form[value] = v
    },
    /**
     * 上传文件
     * @param {Object} param文件参数
     */
    change_file(key_name){
      var _self = this;
      // 选择图像方法
      uni.chooseFile({
        count: 1,
        sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
        sourceType: ['album'], //从相册选择
        success: function(res) {
          const tempFilePaths = res.tempFilePaths;
          const uploadTask = uni.uploadFile({
            url: _self.$fullUrl('/api/food_information/upload?'),
            filePath: tempFilePaths[0],
            name: 'file',
            formData: {
              'i_want_to_customize': 'test'
            },
            header: {
              'x-auth-token': _self.$store.state.user.token
            },
            success: function(uploadFileRes) {
              var filename = JSON.parse(uploadFileRes.data).result.url
              _self.form[key_name] = filename
            }
          });

          uploadTask.onProgressUpdate(function(res) {
            _self.percent = res.progress;
            console.log('上传进度' + res.progress);
            console.log('已经上传的数据长度' + res.totalBytesSent);
            console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);
          });
        },
        error: function(e) {
          console.log(e);
        }
      });
    },
    /**
     * 上传图片
     * @param {Object} param文件参数
     */
    change_img(key_name){
      var _self = this;
      _self.upload_img_flag = false
      // 选择图像方法
      uni.chooseImage({
        count: 1,
        sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
        sourceType: ['album'], //从相册选择
        success: function(res) {
          const tempFilePaths = res.tempFilePaths;
          const uploadTask = uni.uploadFile({
            url: _self.$fullUrl('/api/food_information/upload?'),
            filePath: tempFilePaths[0],
            name: 'file',
            formData: {
              'i_want_to_customize': 'test'
            },
            header: {
              'x-auth-token': _self.$store.state.user.token
            },
            success: function(uploadFileRes) {
              var filename = JSON.parse(uploadFileRes.data).result.url
              _self.form[key_name] = filename
            }
          });

          uploadTask.onProgressUpdate(function(res) {
            _self.percent = res.progress;
            console.log('上传进度' + res.progress);
            console.log('已经上传的数据长度' + res.totalBytesSent);
            console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);
          });
        },
        error: function(e) {
          console.log(e);
        }
      });
    },
    /**
     * 获取对象后获取缓存表单
     * @param {Object} json
     * @param {Object} func
     */
    get_obj_before(param){
      var form = uni.db.get("form");
      if (form) {
        delete(form.examine_state)
        delete(form.examine_reply)
        this.obj = uni.push(this.obj ,form);
        this.form = uni.push(this.form ,form);
      }
      var arr = []
      for (let key in form) {
        arr.push(key)
      }
      for (var i=0;i<arr.length;i++){
        this.disabledObj[arr[i] + '_isDisabled'] = true
      }
                                                      uni.db.del("form");
      return param;
    },
        
            
            
            
            
            
    
    /**
     * 获取对象之后
     * @param {Object} json
     * @param {Object} func
     */
    get_obj_after(json, func){
                                                    },

    is_view(){
      var bl = this.user_group == "管理员";

      if(!bl){
        bl = this.$check_action('/food_information/table','add');
        console.log(bl ? "你有表格添加权限视作有添加权限" : "你没有表格添加权限");
      }
      if(!bl){
        bl = this.$check_action('/food_information/table','set');
        console.log(bl ? "你有表格添加权限视作有修改权限" : "你没有表格修改权限");
      }
      if(!bl){
        bl = this.$check_action('/food_information/view','add');
        console.log(bl ? "你有视图添加权限视作有添加权限" : "你没有视图添加权限");
      }
      if(!bl){
        bl = this.$check_action('/food_information/view','set');
        console.log(bl ? "你有视图修改权限视作有修改权限" : "你没有视图修改权限");
      }
      if(!bl){
        bl = this.$check_action('/food_information/view','get');
        console.log(bl ? "你有视图查询权限视作有查询权限" : "你没有视图查询权限");
      }

      console.log(bl ? "具有当前页面的查看权，请注意这不代表你有字段的查看权" : "无权查看当前页，请注意即便有字段查询权限没有页面查询权限也不行");

      return bl;
    },

  },
  created() {
                                                  },
}
</script>

